.chat {
  flex: 0.7;
  flex-grow:1;
  overflow-y:scroll;
  padding-bottom: 150px;
}

.chat__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px;
  border-bottom: 1px solid lightgray;
}

.chat__headerRight > p {
    display: flex;
    align-items: center;
    font-size: 14px;
}

.chat__headerRight > p > .MuiSvgIcon-root {
    margin-right: 5px!important;
    font-size: 16px;
}

.chat__headerLeft > p > .MuiSvgIcon-root {
    margin-left: 10px!important;
    font-size: 18px;
}

.chat__channelName {
    text-transform: lowercase;
    display: flex;
}